import React, { useMemo, useState, useEffect } from "react";
import CustomModal from "../Modal/customModal";
import {
  AutoSizer,
  List,
  CellMeasurer,
  CellMeasurerCache,
} from "react-virtualized";
import "react-virtualized/styles.css";
import "./SuccessModal.css";
import ReusableAlert from "../Alert/ReusableAlert";
import { handleExportExcel } from "../../utils/helpers";
import CustomButton from "../Button/Button";
import Pagination from "../Pagination/Pagination";
import { ResizableBox } from "react-resizable";
import "react-resizable/css/styles.css";

const SuccessModal = ({
  show = false,
  onHide = () => {},
  responseData = [],
}) => {
  const itemsPerPage = 15;
  const [currentPage, setCurrentPage] = useState(1);
  const [columnWidths, setColumnWidths] = useState({});

  // Memoized CellMeasurer cache
  const cache = useMemo(() => {
    return new CellMeasurerCache({
      fixedWidth: true,
      defaultHeight: 50,
    });
  }, []);

  // Flatten and transform the nested response data
  const flattenedData = useMemo(() => {
    if (!responseData?.responses) return [];

    return responseData.responses.flat().reduce((acc, item) => {
      // Add the parent item without the child array
      const parentItem = { ...item };
      delete parentItem.child;
      acc.push(parentItem);

      // Add child items if they exist
      if (item.child && Array.isArray(item.child)) {
        item.child.forEach((childItem) => {
          // Create relationship between parent and child
          const childWithParentInfo = {
            ...childItem,
            ParentReference: item["EIN Number"] || item["Title"] || "Unknown", // Dynamic parent reference
            IsChild: true,
            _indent: true, // For visual hierarchy
          };
          acc.push(childWithParentInfo);
        });
      }
      return acc;
    }, []);
  }, [responseData]);

  // Dynamically generate columns based on the response data
  const columns = useMemo(() => {
    if (!responseData || responseData.length === 0) return [];

    // Collect all unique keys from ALL items in the response data
    const allKeys = new Set();
    responseData.forEach((item) => {
      Object.keys(item).forEach((key) => allKeys.add(key));
    });

    // Helper function to format header text

    const formatHeaderText = (key) => {
      // Replace underscores and hyphens with spaces
      const spacedText = key.replace(/[_-]/g, " ");

      // Convert to title case (capitalize first letter of each word)
      return spacedText
        .split(" ")
        .map((word) =>
          word === word.toUpperCase()
            ? word // preserve acronyms like EIN, ID
            : word.charAt(0).toUpperCase() + word.slice(1).toLowerCase()
        )
        .join(" ");
    };

    // Convert keys to column definitions
    return Array.from(allKeys).map((key) => {
      // Set appropriate widths based on column type
      let width = columnWidths[key] || 120;
      // if (
      //   key.toLowerCase().includes("message") ||
      //   key.toLowerCase().includes("description")
      // ) {
      //   width = columnWidths[key] || 40;
      // } else if (key.toLowerCase().includes("status")) {
      //   width = columnWidths[key] || 15;
      // } else if (key.toLowerCase().includes("revision")) {
      //   width = columnWidths[key] || 10;
      // } else if (
      //   key.toLowerCase().includes("ein") ||
      //   key.toLowerCase().includes("number")
      // ) {
      //   width = columnWidths[key] || 25;
      // }

      return {
        key: key,
        header: formatHeaderText(key), // Use the formatted header text
        width: width,
        tooltip: formatHeaderText(key), // Update tooltip too
      };
    });
  }, [responseData, columnWidths]);

  const handleResize = (key, newWidth) => {
    setColumnWidths((prev) => ({ ...prev, [key]: newWidth }));
  };

  // Paginate response data
  const paginatedData = useMemo(() => {
    const start = (currentPage - 1) * itemsPerPage;
    return flattenedData.slice(start, start + itemsPerPage);
  }, [currentPage, flattenedData]);

  const handleExport = () => {
    handleExportExcel(responseData, "upload-results.xlsx");
  };

  console.log("SuccessModal received response data:", responseData);
  console.log("Generated columns:", columns);

  useEffect(() => {
    cache.clearAll();
  }, [paginatedData, cache]);

  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
  };

  // Safe getter function to handle case sensitivity and missing properties
  const getItemValue = (item, key) => {
    if (!item || typeof item !== "object") return "-";

    // Direct match
    if (item[key] !== undefined) return item[key];

    // Case-insensitive match
    const lowerKey = key.toLowerCase();
    for (const k of Object.keys(item)) {
      if (k.toLowerCase() === lowerKey) {
        return item[k];
      }
    }

    return "-";
  };

  // Update the row renderer to handle parent-child relationships
  const rowRenderer = ({ key, index, style, parent }) => {
    const item = paginatedData[index];
    if (!item) return null;

    const isChild = item.IsChild;

    return (
      <CellMeasurer
        key={key}
        cache={cache}
        columnIndex={0}
        rowIndex={index}
        parent={parent}
      >
        <div
          style={{
            ...style,
            backgroundColor: isChild ? "#f8fafc" : "#ffffff",
          }}
          className={`virtualized-row ${isChild ? "child-row" : ""}`}
        >
          {columns.map((column, colIndex) => {
            const value = getItemValue(item, column.key);

            // Special styling for child rows
            const cellStyle = {
              width: column.width,
              paddingLeft: isChild && colIndex === 0 ? "2rem" : undefined,
              borderLeft: isChild ? "2px solid #e2e8f0" : undefined,
            };

            // Status cell special handling
            if (column.key.toLowerCase().includes("status")) {
              const statusValue = String(value).toLowerCase();
              const statusClass = statusValue.includes("success")
                ? "success"
                : statusValue.includes("fail") || statusValue.includes("error")
                ? "failed"
                : "";

              return (
                <div
                  key={colIndex}
                  className={`virtualized-cell status-cell ${statusClass}`}
                  style={cellStyle}
                  title={String(value)}
                >
                  {value}
                </div>
              );
            }

            // Default cell rendering
            return (
              <div
                key={colIndex}
                className="virtualized-cell"
                style={cellStyle}
                title={String(value)}
              >
                {String(value)}
              </div>
            );
          })}
        </div>
      </CellMeasurer>
    );
  };

  return (
    <CustomModal
      show={show}
      onHide={onHide}
      title="Upload Results"
      size="lg"
      footerButtons={[
        {
          label: "Close",
          variant: "danger",
          onClick: onHide,
        },
      ]}
    >
      <div className="success-modal-content">
        <div className="top-bar">
          <div></div>
          <CustomButton
            variant="success"
            size="lg"
            onClick={handleExport}
            className="m-2 border-bottom-10px"
            text="Export to Excel "
          />
        </div>

        <ReusableAlert
          variant="success"
          message={
            <p className="success-message-text">
              ✅ <strong>Success:</strong> {responseData.length} item
              {responseData.length > 1 ? "s" : ""} processed.
            </p>
          }
          className="mb-3"
        />

        {columns.length > 0 ? (
          <>
            <div className="virtualized-table-container">
              <div className="virtualized-header" style={{ display: "flex" }}>
                {columns.map((column, idx) => (
                  <ResizableBox
                    key={column.key}
                    width={column.width}
                    height={30}
                    axis="x"
                    resizeHandles={["e"]}
                    handle={
                      <span
                        className="custom-resizer"
                        style={{
                          cursor: "col-resize",
                          width: 8,
                          height: "100%",
                          display: "inline-block",
                          position: "absolute",
                          right: 0,
                          top: 0,
                        }}
                      />
                    }
                    onResizeStop={(e, data) =>
                      handleResize(column.key, data.size.width)
                    }
                    minConstraints={[90, 30]}
                    maxConstraints={[200, 30]}
                  >
                    <div
                      className="virtualized-header-cell"
                      style={{
                        width: column.width,
                        minWidth: 90,
                        maxWidth: 200,
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        position: "relative",
                        userSelect: "none",
                        paddingRight: 8,
                        boxSizing: "border-box",
                      }}
                      title={column.header}
                    >
                      {column.header}
                    </div>
                  </ResizableBox>
                ))}
              </div>

              <div className="virtualized-body">
                <AutoSizer>
                  {({ height, width }) => (
                    <List
                      width={width}
                      height={height - 10}
                      rowCount={paginatedData.length}
                      rowHeight={cache.rowHeight}
                      deferredMeasurementCache={cache}
                      rowRenderer={rowRenderer}
                      overscanRowCount={5}
                    />
                  )}
                </AutoSizer>
              </div>
            </div>

            {/* Pagination Controls */}
            <Pagination
              currentPage={currentPage}
              totalItems={responseData.length}
              itemsPerPage={itemsPerPage}
              onPageChange={handlePageChange}
            />
          </>
        ) : (
          <div className="no-data-message">No response data available.</div>
        )}
      </div>
    </CustomModal>
  );
};

export default SuccessModal;
